und-color: inherit;\n    margin: 0rem 1rem;\n    padding: .5rem 1rem;\n    border-bottom: 2px solid lightgray;\n    justify-content: flex-start;\n    font-size: 1.3rem;\n    color: rgb(41, 41, 41);\n    position: relative;\n}\n\n.project:hover,\n.presort-container > *:hover {\n    cursor: grab;\n    background-color: rgb(199, 199, 199);\n}\n\n.project-delete {\n    background-color: inherit;\n    position: absolute;\n    right: .2rem;\n    align-self: center;\n    font-size: 1rem;\n}\n\n.project-delete:hover {\n    cursor: pointer;\n    color: whitesmoke;\n}\n\n.todo-container {\n    display: flex;\n    justify-content: center;\n}\n\n.todo {\n    display: flex;\n    flex-direction: column;\n    width: 20rem;\n    border-radius: 10px;\n    color: rgb(0, 31, 0);\n}\n\n.completed-todo.todo {\n    box-shadow: 0px 0px 20px -2px rgb(150, 150, 150);\n}\n\n.blue.todo {\n    box-shadow: 0px 0px 20px -2px rgb(132, 140, 158);\n}\n\n.yellow.todo {\n    box-shadow: 0px 0px 20px -2px rgb(158, 154, 132);\n}\n\n.red .todo {\n    box-shadow: 0px 0px 20px -2px rgb(158, 134, 132);\n}\n\n.todo-header {\n    display: flex;\n    border-top-right-radius: 10px;\n    border-top-left-radius: 10px;\n    padding: 0.5rem;\n    align-items: center;\n    position: relative;\n}\n\n.completed-todo .todo-header {\n    background-color: rgb(202, 202, 202);\n    border: 2px solid rgb(121, 121, 121);  \n}\n\n.blue .todo-header {\n    background-color: rgb(85, 127, 218);\n    border: 2px solid rgb(0, 19, 128);\n}\n\n.yellow .todo-header {\n    background-color: rgb(218, 209, 85);\n    border: 2px solid rgb(128, 126, 0);\n}\n\n.red .todo-header {\n    background-color: rgb(218, 94, 85);\n    border: 2px solid rgb(128, 19, 0);\n}\n\n.active,\n.completed {\n    display: grid;\n    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));\n    grid-auto-rows: min-content;\n    align-items: start;\n    gap: 1rem;\n    padding: 0 2rem;\n}\n\n.active-header,\n.completed-header {\n    font-size: 2rem;\n    